import React, { useEffect, useState, useCallback } from 'react';
import AppLayout from '../components/AppLayout';
import Head from 'next/head';
import { END } from 'redux-saga';
import axios from 'axios';
import useSWR from 'swr';

import NicknameEditForm from '../components/NicknameEditForm';
import FollowList from '../components/FollowList';
import { useSelector } from 'react-redux';
import Router from 'next/router';
import { LOAD_MY_INFO_REQUEST } from '../reducers/user';
import wrapper from '../store/configureStore';

const fetcher = (url) => axios.get(url, { withCredentials: true }).then((result) => result.data);

const Profile = () => {
    //const dispatch = useDispatch();
    const { me } = useSelector((state) => state.user);
    const [followersLimit, setFollowersLimit] = useState(3);
    const [followingsLimit, setFollowingsLimit] = useState(3);
    
    const { data: followersData, error: followerError } = useSWR(`http://localhost:3065/user/followers?limit=${followersLimit}`, fetcher);
    const { data: followingsData, error: followingError } = useSWR(`http://localhost:3065/user/followings?limit=${followingsLimit}`, fetcher);

    // useEffect(() => {
    //     dispatch({ type: LOAD_FOLLOWERS_REQUEST });
    //     dispatch({ type: LOAD_FOLLOWINGS_REQUEST });
    // }, []);

    useEffect(() => { if (!(me && me.id)) { Router.push('/'); } }, [me && me.id]);

    const loadMoreFollowings = useCallback(() => { setFollowingsLimit((prev) => prev + 3); }, []);
    const loadMoreFollowers = useCallback(() => { setFollowersLimit((prev) => prev + 3); }, []);

    if (!me) { return <div>  .</div>; }

    if (followerError || followingError) {
        console.error(followerError || followingError);
        return '/    ';
    }
    
    return (
        <>
            <Head>
                <title>  | NodeBird</title>
            </Head>
            <AppLayout>
                <NicknameEditForm />
                <FollowList header=" " data={followingsData/*me.Followings*/} onClickMore={loadMoreFollowings} loading={!followingsData && followingError} />
                <FollowList header=" " data={followersData/*me.Followers*/} onClickMore={loadMoreFollowers} loading={!followersData && followerError} />
            </AppLayout>
        </>
    );
};

export const getServerSideProps = wrapper.getServerSideProps(async (context) => {
    console.log('getServerSideProps ');
    console.log('', context.req.headers);
    
    const cookie = context.req ? context.req.headers.cookie : '';
    
    axios.defaults.headers.Cookie = '';
    if (context.req && cookie) { axios.defaults.headers.Cookie = cookie; }

    context.store.dispatch({ type: LOAD_MY_INFO_REQUEST });
    
    context.store.dispatch(END);
    console.log('getServerSideProps ');
    
    await context.store.sagaTask.toPromise();
});

export default Profile;